module.exports = {
  data: function () {
    let self = this
    return {
      zoom: 20,
      center: [116.397477, 39.908692],
      searchOption: {
        city: '全国',
        citylimit: true
      },
      marker: {
        position: [116.397477, 39.908692]
      },
      address: '',
      events: {
        click (e) {
          let { lng, lat } = e.lnglat
          self.lng = lng
          self.lat = lat
          self.marker.position = [lng, lat]
          // 这里通过高德 SDK 完成。
          self.getAddress(lng, lat)
        }
      },
      lng: 0,
      lat: 0,
      oldParams: null
    }
  },
  mounted () {
    let query = this.$route.query
    this.oldParams = query.params
    if(query.params.postLongitude == 0 && query.params.postLatitude == 0) {
        query.params.postLongitude = 104.065735
        query.params.postLatitude = 30.659462
    }
    console.log(this.$route.query)
    if (query.params) {
      if (query.params.postLongitude && query.params.postLatitude) {
        this.getAddress(query.params.postLongitude, query.params.postLatitude)
      }
      this.address = query.params.postLocation
    } else {
      this.getAddress(104.065735,30.659462)
      let that = this
      geocoder.getAddress([104.065735,30.659462], function (status, result) {
        if (status === 'complete' && result.info === 'OK') {
          if (result && result.regeocode) {
            that.address = result.regeocode.formattedAddress
            that.$nextTick()
          }
        }
      })
    }
  },
  methods: {
    getBack () {
      this.$router.replace({
          name: 'houseAdd',
        query: {
              params: this.oldParams
        }
      })
    },
    addAddress () {
      this.oldParams.postLongitude = this.lng
      this.oldParams.postLatitude = this.lat
      this.oldParams.postLocation = this.address
      this.$router.replace({
        name: 'houseAdd',
        query: {
          params: this.oldParams
        }
      })
    },

    getAddress (lng, lat) {
      this.lng = lng
      this.lat = lat
      this.center = [lng, lat]
      this.marker.position = [lng, lat]
      var geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: 'all'
      })
      let that = this
      geocoder.getAddress([lng, lat], function (status, result) {
        if (status === 'complete' && result.info === 'OK') {
          if (result && result.regeocode) {
            that.address = result.regeocode.formattedAddress
            console.log(lng, lat)
            that.$nextTick()
          }
        }
      })
    },
    onSearchResult (pois) {
      var lng = pois[0].lng
      var lat = pois[0].lat
      this.getAddress(lng, lat)
    }
  }
}



